<template>
    <header>
        <h1>
            <img src="../assets/logo.png" alt="">
        </h1>
        <button class="button" @click="toggleDarkMode">
            {{buttonText}}
        </button>
    </header>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'SideBar',
    emits:['onToggleDarkMode'],
    data (){
        return {
            darkMode: false,
        }
    },
    computed:{
        buttonText () {
            if (this.darkMode) {
                return 'Disable Dark Mode';
            }
            else {
                return 'Active Dark Mode';
            }
        }
    },
    methods: {
        toggleDarkMode(): void {
            this.darkMode = !this.darkMode;
            this.$emit('onToggleDarkMode', this.darkMode);
        }
    }
});

</script>

<style scoped>
    header {
        padding: 1rem;
        background-color: #11395e;
        width: 100%;
        height: 100vh;
        text-align: center;
    }
    @media only screen and (max-width: 768px) {
        header {
            padding: 2.5rem;
            height: auto;
        }
    }
</style>